@import url('https://fonts.googleapis.com/css?family=Roboto:100,300,400,500,700,900&display=swap');

///////////////////////////////////////////////////////////////////////////////////////////////////
// Breakpoints
///////////////////////////////////////////////////////////////////////////////////////////////////

$breakpoint-tablet: 1200px;
$breakpoint-mobile: 600px;

///////////////////////////////////////////////////////////////////////////////////////////////////
// Font families
///////////////////////////////////////////////////////////////////////////////////////////////////

$font-family-base: 'Roboto', sans-serif;
$font-family-h1: $font-family-base;
$font-family-h2: $font-family-base;
$font-family-h3: $font-family-base;
$font-family-h4: $font-family-base;
$font-family-h5: $font-family-base;
$font-family-h6: $font-family-base;
$font-families: $font-family-h1, $font-family-h2, $font-family-h3, $font-family-h4, $font-family-h5, $font-family-h6;

.font-family-base {
    font-family: $font-family-base !important;
}

@for $i from 1 through 6 {
    .font-family-h#{$i} {
        font-family: nth($font-families, $i) !important;
    }
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Font sizes
///////////////////////////////////////////////////////////////////////////////////////////////////

$font-size-tiny: 1.0rem;
$font-size-small: 1.3rem;
$font-size-base: 1.6rem;
$font-size-large: 1.9rem;
$font-size-huge: 2.2rem;
$font-size-h1: 2.00 * $font-size-base;
$font-size-h2: 1.75 * $font-size-base;
$font-size-h3: 1.50 * $font-size-base;
$font-size-h4: 1.50 * $font-size-base;
$font-size-h5: 1.25 * $font-size-base;
$font-size-h6: 1.00 * $font-size-base;
$font-sizes: $font-size-h1, $font-size-h2, $font-size-h3, $font-size-h4, $font-size-h5, $font-size-h6;

.font-size-tiny {
    font-size: $font-size-tiny !important;
}

.font-size-small {
    font-size: $font-size-small !important;
}

.font-size-base {
    font-size: $font-size-base !important;
}

.font-size-large {
    font-size: $font-size-large !important;
}

.font-size-huge {
    font-size: $font-size-huge !important;
}

@for $i from 1 through 6 {
    .font-size-h#{$i} {
        font-size: nth($font-sizes, $i) !important;
    }
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Font weights
///////////////////////////////////////////////////////////////////////////////////////////////////

$font-weight-thin: 100;
$font-weight-light: 300;
$font-weight-base: 400;
$font-weight-bold: 700;
$font-weight-heavy: 900;
$font-weight-h1: $font-weight-bold;
$font-weight-h2: $font-weight-bold;
$font-weight-h3: $font-weight-bold;
$font-weight-h4: $font-weight-base;
$font-weight-h5: $font-weight-base;
$font-weight-h6: $font-weight-base;
$font-weights: $font-weight-h1, $font-weight-h2, $font-weight-h3, $font-weight-h4, $font-weight-h5, $font-weight-h6;

.font-weight-thin {
    font-weight: $font-weight-thin !important;
}

.font-weight-light {
    font-weight: $font-weight-light !important;
}

.font-weight-base {
    font-weight: $font-weight-base !important;
}

.font-weight-bold {
    font-weight: $font-weight-bold !important;
}

.font-weight-heavy {
    font-weight: $font-weight-heavy !important;
}

@for $i from 1 through 6 {
    .font-weight-h#{$i} {
        font-weight: nth($font-weights, $i) !important;
    }
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Font styles
///////////////////////////////////////////////////////////////////////////////////////////////////

$font-style-normal: normal;
$font-style-italic: italic;
$font-style-base: $font-style-normal;
$font-style-h1: $font-style-normal;
$font-style-h2: $font-style-normal;
$font-style-h3: $font-style-normal;
$font-style-h4: $font-style-normal;
$font-style-h5: $font-style-normal;
$font-style-h6: $font-style-normal;
$font-styles: $font-style-h1, $font-style-h2, $font-style-h3, $font-style-h4, $font-style-h5, $font-style-h6;

.font-style-normal {
    font-style: $font-style-normal !important;
}

.font-style-italic {
    font-style: $font-style-italic !important;
}

@for $i from 1 through 6 {
    .font-style-h#{$i} {
        font-style: nth($font-styles, $i) !important;
    }
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Fonts
///////////////////////////////////////////////////////////////////////////////////////////////////

@mixin font-base(
    $font-family: $font-family-base,
    $font-weight: $font-weight-base,
    $font-style: $font-style-base,
    $font-size: $font-size-base) {
    font-family: $font-family;
    font-weight: $font-weight;
    font-style: $font-style;
    font-size: $font-size;
}

@mixin font-h(
    $number, 
    $font-family: nth($font-families, $number),
    $font-weight: nth($font-weights, $number),
    $font-style: nth($font-styles, $number),
    $font-size: nth($font-sizes, $number)) {
    font-family: $font-family;
    font-weight: $font-weight;
    font-style: $font-style;
    font-size: $font-size;
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Offsets
///////////////////////////////////////////////////////////////////////////////////////////////////

$offset-tiny: 0.1rem;
$offset-small: 0.5rem;
$offset-regular: 1rem;
$offset-large: 2rem;
$offset-huge: 4rem;

///////////////////////////////////////////////////////////////////////////////////////////////////
// Colors
///////////////////////////////////////////////////////////////////////////////////////////////////

$color-plain-white: #ffffff;
$color-plain-black: #000000;

$color-brand-white: $color-plain-white;
$color-brand-dark-grey: #383339;
$color-brand-light-grey: #3e393f;
$color-brand-accent-grey: #a49da6; // TODO: check if requied; check naming
$color-brand-accent-pink: #fe6466; // TODO: check if requied
$color-brand-accent-red: #e74c3c;

$color-dark-text: $color-brand-dark-grey;
$color-light-text: $color-brand-white;
$color-dark-background: $color-brand-dark-grey;
$color-light-background: $color-brand-white;
$color-main-background: #f0f0f0;

///////////////////////////////////////////////////////////////////////////////////////////////////
// Backgrounds
///////////////////////////////////////////////////////////////////////////////////////////////////

.background-plain-white {
    background-color: $color-plain-white !important;
}

.background-plain-black {
    background-color: $color-plain-black !important;
}

.background-brand-dark-grey {
    background-color: $color-brand-dark-grey !important;
}

.background-brand-dark-grey {
    background-color: $color-brand-dark-grey !important;
}

.background-brand-light-grey {
    background-color: $color-brand-light-grey !important;
}

.background-brand-accent-red {
    background-color: $color-brand-accent-red !important;
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Helpers
///////////////////////////////////////////////////////////////////////////////////////////////////

.display-flex-row {
    display: flex !important;
    flex-flow: row !important;
}

.display-flex-row-wrap {
    display: flex !important;
    flex-flow: row wrap !important;
}

.display-flex-column {
    display: flex !important;
    flex-flow: column !important;
}

.display-flex-column-wrap {
    display: flex !important;
    flex-flow: column wrap !important;
}

.flex-min {
    flex: 0 !important;
}

.flex-auto {
    flex: auto !important;
}

.flex-max {
    flex: 100% !important;
}

.text-align-left {
    text-align: left !important;
}

.text-align-center {
    text-align: center !important;
}

.text-align-right {
    text-align: right !important;
}

.display-inline-block {
    display: inline-block;
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Styling
///////////////////////////////////////////////////////////////////////////////////////////////////

:root {
    font-size: 62.5%;
}

html, body {
    @include font-base;
    
    margin: 0;
    background-color: $color-light-background;
    color: $color-dark-text;
    height: 100%;
}

main {
    width: 100%;
    background-color: $color-main-background;
}

///////////////////////////////////////////////////////////////////////////////////////////////////
// Headers, paragraphs etc.
///////////////////////////////////////////////////////////////////////////////////////////////////

@for $i from 1 through 6 {
    h#{$i}, .h#{$i} {
        @include font-h($number: $i);
    }
}

p, a, address {
    @include font-base;
}

.display {
    font-weight: $font-weight-thin !important;
}

///////////////////////////////////////////////////////////////////////////////////
// Text utility classes
///////////////////////////////////////////////////////////////////////////////////

a {
    color: $color-brand-accent-red;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
        // text-underline-position: 1rem;
    }

    /*&:visited {
        color: $color-brand-light-grey;
    }*/

    &[target="_blank"] {
        &:after {
            content: "";
            display: inline-block;
        }
    }
}

blockquote {
    font-weight: $font-weight-light !important;
}

strong {
    font-weight: $font-weight-bold !important;
}

header {
    height: 5rem;
    background-color: $color-brand-dark-grey;
}

footer {
    height: 3rem;
    background-color: $color-brand-dark-grey;    
}

button, a.button, input[type="button"], input[type="submit"] {
    @include font-base;

    display: inline;
    border: 0.1rem solid $color-brand-dark-grey;
    background: $color-brand-white;
    border-radius: 0.4rem;
    color: $color-brand-dark-grey;
    padding: 1rem 2rem;
    text-decoration: none;

    &:disabled {
        color: $color-brand-accent-grey;
        border-color: $color-brand-accent-grey;
    }

    &.accent {
        border-color: $color-brand-accent-red;
        color: $color-brand-accent-red;

        &:disabled {
            color: $color-brand-white;
            border-color: $color-brand-accent-grey;
        }
    }

    &.primary {
        background: $color-brand-dark-grey;
        color: $color-brand-white;

        &:disabled {
            background: $color-brand-accent-grey;
        }

        &.accent {
            background: $color-brand-accent-red;

            &:disabled {
                background: $color-brand-accent-grey;
            }
        }
    }
}

input, textarea {
    @include font-base;
    
    border: 0.1rem solid $color-brand-dark-grey;
    border-radius: 0;
    padding: $offset-regular;

    &:focus {
        outline-offset: 0.2rem;
        outline-color: $color-brand-accent-red;
    }
}

label {
    margin-bottom: $offset-small;
}

.form-error {
    font-size: $font-size-small;
    color: $color-brand-accent-red;
}

label.required::after {
    content: " *";
    color: $color-brand-accent-red;
}

label.required-description::before {
    content: "* ";
    color: $color-brand-accent-red;
}

.alternate-brand-light-grey {
    background-color: $color-brand-light-grey;
}

.alternate-brand-accent-grey {
    background-color: $color-brand-accent-grey;
}

.alternate-brand-accent-red {
    background-color: $color-brand-accent-red;
}

figcaption mark {
    background-color: $color-brand-white;
}

///////////////////////////////////////////////////////////////////////////////////
// Site content features
///////////////////////////////////////////////////////////////////////////////////

.loading {
    display: flex;
    place-items: center;
    place-content: center;
    place-self: center;
    height: 100%;
}

.jumbo {
    display: flex;
    min-height: 100%;
}

.slide {
    display: flex;
    flex-flow: column;
    width: 100%;

    h1 {
        color: $color-brand-white;
        background-color: $color-brand-accent-red;
        padding-left: $offset-large;
        padding-right: $offset-large;
        font-size: 10rem;
        place-self: flex-start;
        text-transform: uppercase;
    }

    p {
        margin: $offset-small 0;
        padding: $offset-regular $offset-regular;
        background-color: $color-light-background;
        color: $color-dark-text;
        place-self: flex-start;
    }

    div {
        flex: 1;
        display: flex;
        flex-flow: row;
        place-content: center;
    }

    a:not(.button) {
        text-transform: uppercase;
    }

    a.button.primary.accent {
        place-self: flex-end;
        margin: $offset-huge $offset-regular 7rem $offset-regular;
    }
}

.intro {
    width: 100%;
    height: 30rem;

    img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

.content {
    padding: $offset-large 20%;
    background-color: $color-main-background;

    h1, h2, h3, h4, h5, h6 {
        text-transform: uppercase;
        text-align: left;
    }

    p {
        text-align: justify;
        line-height: 2.5rem;
    }
}

.testimony {
    display: flex;
    place-items: center;
    place-content: center;

    blockquote {
        display: flex;
        flex-flow: row wrap;
        max-width: 80rem;

        img {
            border: 0.3rem solid $color-brand-accent-grey;
            border-radius: 50%;
            width: 8rem;
            height: 8rem;
        }

        div {
            flex: 1;
            margin-left: $offset-huge;
            text-align: justify;
        }

        p {
            text-align: right;
            margin-bottom: 0;
        }
    }
}

.cta {
    padding: 0 20% $offset-huge 20%;
    background-color: $color-main-background;
    text-align: center;
}

.featured {
    display: flex;
    flex-flow: row wrap;
    place-items: center;
    padding: $offset-huge 25%;
    background-color: $color-light-background;

    &, p, h1, h2, h3, h4, h5, h6 {
        background-color: $color-light-background;
        font-family: $font-family-base !important;
        font-weight: $font-weight-light !important;
        margin-bottom: 0;

        &:first-child {
            margin-top: 0;
        }
    }

    &.project {
        img {
            max-width: 15rem;
            max-height: 15rem;
        }

        div {
            // flex: 1;
            flex: 1;
            margin-left: $offset-huge;
        }

        &:hover img {
            transition: opacity 0.5s, filter 0.5s;
            filter: none;
            opacity: 1;
        }

        &:not(:hover) img {
            transition: opacity 0.5s, filter 0.5s;
            filter: grayscale(100%);
            opacity: 0.4;
        }
/*
        p {
            text-align: right;

        }*/
    }
}

@media (max-width: $breakpoint-tablet) {
    .slide h1 {
        font-size: 7rem;
    }

    .content {
        padding: $offset-large 10%;
    }
}

@media (max-width: $breakpoint-mobile) {
    .slide h1 {
        font-size: $font-size-h1;
    }

    .content {
        padding: $offset-large $offset-large;
    }

    .testimony {
        blockquote {
            flex-flow: column !important;
            align-items: center;

            div {
                margin-top: $offset-large;
                margin-left: 0 !important;
            }

            p {
                text-align: right;
            }
        }
    }

    .featured {
        padding: $offset-huge;

        &.project {
            place-content: center;
            text-align: center;

            img {
                flex-basis: 100% !important;
                margin-bottom: $offset-huge;
            }

            div {
                flex: auto;
                margin: 0;
            }
        }
    }
}