@import "../framework/site.scss";

#dummy {
    padding: 1rem;
    background-color: lime;

    div {
        display: flex;
        flex-flow: column;
        margin: $offset-small;

        &.engineering:not(.instagram) {
            width: 120rem;
            height: 62.7rem;
            background-image: url("../assets/og/0-digitalize.jpg");
        }

        &.engineering.instagram {
            width: 108rem;
            height: 100rem;
            background-image: url("../assets/instagram/0-digitalize.jpg");
        }

        &.improvement:not(.instagram) {
            width: 120rem;
            height: 62.7rem;
            background-image: url("../assets/og/1-modernize.jpg");
        }

        &.improvement.instagram {
            width: 108rem;
            height: 100rem;
            background-image: url("../assets/instagram/1-modernize.jpg");
        }

        &.consultancy:not(.instagram) {
            width: 120rem;
            height: 62.7rem;
            background-image: url("../assets/og/2-transform.jpg");
        }

        &.consultancy.instagram {
            width: 108rem;
            height: 100rem;
            background-image: url("../assets/instagram/2-transform.jpg");
        }

        &.recruitment:not(.instagram) {
            width: 120rem;
            height: 62.7rem;
            background-image: url("../assets/og/3-innovate.jpg");
        }

        &.recruitment.instagram {
            width: 108rem;
            height: 100rem;
            background-image: url("../assets/instagram/3-innovate.jpg");
        }

        &.contact:not(.instagram) {
            width: 120rem;
            height: 62.7rem;
            background-image: url("../assets/og/4-contact.jpg");
        }

        &.contact.instagram {
            width: 108rem;
            height: 100rem;
            background-image: url("../assets/instagram/4-contact.jpg");
        }
    }

    h1 {
        color: $color-brand-white;
        background-color: $color-brand-accent-red;
        padding-left: $offset-large;
        padding-right: $offset-large;
        font-size: 14rem;
        place-self: flex-start;
        text-transform: uppercase;
    }

    p {
        margin: $offset-small 0;
        padding: $offset-large $offset-large;
        background-color: $color-light-background;
        color: $color-dark-text;
        place-self: flex-start;
        font-size: 3.5rem;
    }

    .instagram h1 {
        font-size: 13.57rem;
    }

    .instagram p {
        font-size: 5rem;
    }
}
